___CSS_LOADER_EXPORT___.push([module.id, `/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  Typographic tweaks!
  3. Add accessible line-height
  4. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  5. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
  6. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  7. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
  8. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

body {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 50px 1fr 50px;
  grid-template-columns: 1fr;
}

header {
  display: flex;
  justify-content: center;
}

.headerFooter {
  background-color: black;
  color: white;
}

main {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  align-content: center;
  align-self: center;
}

.boards {
  height: 30%;
  width: 80%;
  justify-self: center;
  justify-content: center;
  justify-items: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  gap: 20px;
}

.player {
  display: grid;
  gap: 10px;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(10, 1fr);
}
.row {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 1fr;
}

.space {
  background-color: rgba(52, 114, 11, 0.795);
  width: 60px;
  height: 60px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.space:hover {
  background-color: #ccc;
}

.yaks {
  display: flex;

  justify-content: space-evenly;
}

.yaks > * {
  opacity: 1;
}

.hidden {
  opacity: 0.3;
}

#placementForm {
  display: grid;
  gap: 10px;
  padding: 18px;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 8px;
}
